@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&family=Roboto:ital,wght@1,100&display=swap');

.form-product-cont {
    width: 100vw;
    height: 100vh;
    background: white;
    position: relative;
}

.form {
    width: 45%;
    height: 80%;
    border-radius: 20px;
    border: 1px solid black;
    padding: 10px;
}

.form-info {
    height: 100%;
    width: 100%;
}

.sections-form {
    width: 80%;
    height: 25%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.form-info input {
    width: 100%;
    padding: 5px;
}

.input-text {
    border: 1px solid black;
    border-radius: 10px;
}

.input-file {
    width: 80%;
    height: 25%;
}

.input-file input,
.select-category {
    width: 40%;
}

.input-file .select-category {
    padding: 3px;
    border: 1px solid gray;
    border-radius: 1px;
}

.send {
    padding: 10px 20px;
    border-radius: 20px;
    background: #7CFF7A;
    transition: 0.5s;
}

.send:hover {
    transform: scale(1.1);
}

.back {
    position: absolute;
    top: 5vh;
    left: 3vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 10px;
    border: 1px solid black;
    border-radius: 10px;
}

.back:hover {
    background: black;
    color: white;
}

input::-moz-placeholder {
    color: gray;
}

input::placeholder {
    color: gray;
}

.description-cont {
    margin: 20px;
    width: 80%;
    height: 30vh;
}

textarea {
    width: 100%;
    height: 100%;
    border: 1px solid black;
    border-radius: 10px;
    margin-bottom: 20px;
}

.sup {
    width: 100%;
    height: 80%;
}

.form-info label {
    color: #2d79f3;
}

